/*
  Accordion
*/

@use "variables" as *;

.accordion {
  border-top: 1px solid #cdd1d5;

  .accordion-item {
    //padding: 4px 0;
    //transition: 0.4s ease-in-out;
    border-bottom: 1px solid #cdd1d5;

    .btn-accordion {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      padding: 20px 50px 20px 24px;
      background-color: $white;
      text-align: left;
      font-size: 19px;
      border-radius: 10px;
      gap: 30px;

      .accordion-title {
        display: flex;
        gap: 30px;

        .category {
          font-size: 19px;
          min-width: 72px;
        }
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: 24px;
        width: 24px;
        height: 24px;
        background-image: url(../images/common/ico-arrodion.svg);
        transition: 0.2s ease-in-out;
        transform: translateY(-50%) rotate(0deg);
      }

      &.active {
        &::after {
          transform: translateY(-50%) rotate(180deg);
        }
      }
    }

    .ico-acc {
      flex-shrink: 0;
      display: inline-block;
      width: 36px;
      height: 36px;
      margin-bottom: auto;
      font-size: 19px;
      font-weight: bold;
      text-align: center;
      line-height: 36px;
      border-radius: 6px;
      border: 1px solid transparent;

      &.question {
        background-color: #ecf4fe;
        border-color: #256ef4;
        color: #256ef4;
      }

      &.answer {
        background-color: #256ef4;
        border-color: #256ef4;
        color: $white;
      }
    }

    .accordion-collapse {
      max-height: 0;
      overflow: hidden;
      transition: all .2s cubic-bezier(0, 1, 0, 1);
      visibility: hidden;
    }

    .accordion-body {
      display: flex;
      align-items: center;
      gap: 30px;
      padding: 20px 24px;
      font-size: 17px;
      border-radius: 10px;
      background-color: #eef2f7;
      margin-bottom: 20px;
    }

    &.active {
      .accordion-collapse {
        height: auto;
        max-height: 100vh;
        transition: all .2s ease-in-out;
        visibility: visible;
      }

      .btn-accordion {
        .accordion-title {
          font-weight: bold;
          color: #003675;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .accordion {
    .accordion-item {
      .btn-accordion {
        gap: 15px;
        padding: 20px 50px 20px 20px;

        .accordion-title {
          flex-direction: column;
          gap: 15px;
          padding-top: 5px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .accordion {
    .accordion-item {
      .btn-accordion {
        gap: 15px;
        padding: 20px 40px 20px 15px;

        &::after {
          right: 15px;
        }

        .accordion-title {
          flex-direction: column;
          gap: 10px;
          padding-top: 5px;
          font-size: 17px;
        }
      }

      .ico-acc {
        width: 30px;
        height: 30px;
        line-height: 30px;
      }

      .accordion-body {
        padding: 20px 15px;
        gap: 15px;
        font-size: 15px;
      }
    }
  }
}