/*
  Tooltip
*/

.tooltip {
  background: #FFFCF1;
  padding: 10px;
  border: 1px solid #DCCA99;
  font-size: 13px;
  border-radius: 4px;
  display: none;
  line-height: 16px;
  letter-spacing: -0.65px;
  text-align: center;
  color: #934C00;
  //display: flex;
  max-width: 360px;
  justify-content: center;
  align-items: center;
  min-width: 148px;
  min-height: 50px;
  box-shadow: 1px 2px 2px 0 rgba(0, 0, 0, 0.15);
}

.tooltip[data-show] {
  display: flex;
}

.arrow,
.arrow::before {
  position: absolute;
  width: 5px;
  height: 5px;
  background-image: url(../images/common/tool-tip.png);
  background-size: cover;
}

.arrow {
  visibility: hidden;
}

.arrow::before {
  visibility: visible;
  content: '';
  transform: rotate(0);
}

.tooltip[data-popper-placement^='top'] > .arrow {
  bottom: -5px;
}

.tooltip[data-popper-placement^='top'] > .arrow::before {
  transform: rotate(180deg);
}

.tooltip[data-popper-placement^='bottom'] > .arrow {
  top: -5px;
}

.tooltip[data-popper-placement^='bottom'] > .arrow::before {
  transform: rotate(0);
}

.tooltip[data-popper-placement^='left'] > .arrow {
  right: -5px;
}

.tooltip[data-popper-placement^='left'] > .arrow::before {
  transform: rotate(90deg);
}

.tooltip[data-popper-placement^='right'] > .arrow {
  left: -5px;
}

.tooltip[data-popper-placement^='right'] > .arrow::before {
  transform: rotate(270deg);
}