/*
  Common
*/

@use "variables" as *;
@use "config" as *;

/* datepicker */
.ui-icon,
.ui-widget-content .ui-icon {
  display: inline-block;
  background-size: cover;
  background-repeat: no-repeat;
  font-size: 0;
  letter-spacing: 0;
}

.ui-icon-circle-triangle-w {
  width: 10.5px;
  height: 16px;
  background-image: url(../images/common/ico-paging-prev.png);
}

.ui-icon-circle-triangle-e {
  width: 10.5px;
  height: 16px;
  background-image: url(../images/common/ico-paging-next.png);
}

.ui-datepicker {
  width: 17em;
  padding: .2em .2em 0;
  display: none;
}
.ui-datepicker .ui-datepicker-header {
  position: relative;
  padding: .2em 0;
}
.ui-datepicker .ui-datepicker-prev,
.ui-datepicker .ui-datepicker-next {
  position: absolute;
  top: 2px;
  width: 1.8em;
  height: 1.8em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ui-datepicker .ui-datepicker-prev-hover,
.ui-datepicker .ui-datepicker-next-hover {
  top: 1px;
}
.ui-datepicker .ui-datepicker-prev {
  left: 2px;
}
.ui-datepicker .ui-datepicker-next {
  right: 2px;
}
.ui-datepicker .ui-datepicker-prev-hover {
  left: 1px;
}
.ui-datepicker .ui-datepicker-next-hover {
  right: 1px;
}
//.ui-datepicker .ui-datepicker-prev span,
//.ui-datepicker .ui-datepicker-next span {
//  display: block;
//  position: absolute;
//  left: 50%;
//  margin-left: -8px;
//  top: 50%;
//  margin-top: -8px;
//}
.ui-datepicker .ui-datepicker-title {
  margin: 0 2.3em;
  line-height: 1.8em;
  text-align: center;
}
.ui-datepicker .ui-datepicker-title select {
  margin: 1px 0;
  width: 100%;
  height: 40px;
  border: 1px solid #ccc;
  letter-spacing: -0.75px;
  background-color: #fff;
  color: #333;
  font-size: 15px;
  padding: 0 25px 0 10px;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  background-size: 12px 8px;
  background-position: right 11px center;
  background-repeat: no-repeat;
  background-image: url(../images/common/ico-dropdown.svg);
}
.ui-datepicker select.ui-datepicker-month,
.ui-datepicker select.ui-datepicker-year {
  width: 45%;
}
.ui-datepicker table {
  width: 100%;
  font-size: .9em;
  border-collapse: collapse;
  margin: 0 0 .4em;
}
.ui-datepicker th {
  padding: .7em .3em;
  text-align: center;
  font-weight: bold;
  border: 0;
}
.ui-datepicker td {
  border: 0;
  padding: 1px;
}
.ui-datepicker td span,
.ui-datepicker td a {
  display: block;
  padding: .2em;
  text-align: right;
  text-decoration: none;
}
.ui-datepicker .ui-datepicker-buttonpane {
  background-image: none;
  margin: .7em 0 0 0;
  padding: 0 .2em;
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}
.ui-datepicker .ui-datepicker-buttonpane button {
  float: right;
  margin: .5em .2em .4em;
  cursor: pointer;
  padding: .2em .6em .3em .6em;
  width: auto;
  overflow: visible;
}
.ui-datepicker .ui-datepicker-buttonpane button.ui-datepicker-current {
  float: left;
}

.ui-datepicker.ui-datepicker-multi {
  width: auto;
}
.ui-datepicker-multi .ui-datepicker-group {
  float: left;
}
.ui-datepicker-multi .ui-datepicker-group table {
  width: 95%;
  margin: 0 auto .4em;
}
.ui-datepicker-multi-2 .ui-datepicker-group {
  width: 50%;
}
.ui-datepicker-multi-3 .ui-datepicker-group {
  width: 33.3%;
}
.ui-datepicker-multi-4 .ui-datepicker-group {
  width: 25%;
}
.ui-datepicker-multi .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-multi .ui-datepicker-group-middle .ui-datepicker-header {
  border-left-width: 0;
}
.ui-datepicker-multi .ui-datepicker-buttonpane {
  clear: left;
}
.ui-datepicker-row-break {
  clear: both;
  width: 100%;
  font-size: 0;
}

.ui-datepicker-rtl {
  direction: rtl;
}
.ui-datepicker-rtl .ui-datepicker-prev {
  right: 2px;
  left: auto;
}
.ui-datepicker-rtl .ui-datepicker-next {
  left: 2px;
  right: auto;
}
.ui-datepicker-rtl .ui-datepicker-prev:hover {
  right: 1px;
  left: auto;
}
.ui-datepicker-rtl .ui-datepicker-next:hover {
  left: 1px;
  right: auto;
}
.ui-datepicker-rtl .ui-datepicker-buttonpane {
  clear: right;
}
.ui-datepicker-rtl .ui-datepicker-buttonpane button {
  float: left;
}
.ui-datepicker-rtl .ui-datepicker-buttonpane button.ui-datepicker-current,
.ui-datepicker-rtl .ui-datepicker-group {
  float: right;
}
.ui-datepicker-rtl .ui-datepicker-group-last .ui-datepicker-header,
.ui-datepicker-rtl .ui-datepicker-group-middle .ui-datepicker-header {
  border-right-width: 0;
  border-left-width: 1px;
}

.ui-widget-content {
  border: 1px solid $gray-ddd;
  border-radius: 3px;
  font-family: $font;
  background-color: $white;

  .ui-state-default {
    border: 0;
    background-color: transparent;
    text-align: center;
    font-size: 15px;
    color: $black;
  }
}

.ui-widget-header {
  border: 0;
  font-family: $font;
}

.ui-datepicker {
  font-family: $font;

  .ui-datepicker-title {
    margin-top: 10px;
    margin-bottom: 10px;

    > * + * {
      margin-left: 10px !important;
    }

    select {
      border: 0;
      border-bottom: 1px solid $black;
      font-family: $font;
      font-size: 15px;
      font-weight: 500;
    }
  }

  th {
    font-size: 15px;
    font-weight: 500;
    color: $gray-666;
  }

  .ui-datepicker-today {
    a {
      border: 1px solid $point;
      background-color: #EDF2F6;
      border-radius: 100%;
      color: $point;
    }
  }

  td {
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      text-align: center;
      border-radius: 100%;
    }
  }

  .ui-state-active {
    border: 1px solid $point;
    background-color: $point;
    border-radius: 100%;
    color: $white;
    background-image: none;
  }

  .date-holiday {
    a {
      color: $red;
    }
  }

  .date-saturday {
    a {
      color: $point;
    }
  }

  .date-sunday {
    a {
      color: $red;
    }
  }

  .ui-datepicker-buttonpane {
    border: 0;
  }

  .ui-datepicker-current {
    height: 24px;
    border-radius: 3px;
    width: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5px;
    font-size: 15px;
    font-family: $font;
    background-image: none;

    &.ui-priority-secondary {
      background-color: #EDF2F6;
      color: $point;
      border: 1px solid $point;
    }
  }

  .ui-datepicker-close {
    height: 24px;
    border-radius: 3px;
    width: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5px;
    font-size: 15px;
    font-weight: 400;
    font-family: $font;
    background-color: #F2F2F2;
    color: $gray-666;
    border: 1px solid $gray-ccc;
  }

  .ui-datepicker-prev, .ui-datepicker-next {
    top: 14px;
  }
}