/*
  Calendar
*/

@use "variables" as *;

.fc .fc-view-harness {
  background-color: $white;
  border-radius: 10px;
  border: 1px solid #e3e7ee;
  box-shadow: 0 4px 10px 0 rgba(0, 54, 117, 0.1);
  overflow: hidden;
}

.fc-theme-standard {
  td, td {
    border: 0;
    border-bottom: 1px solid $gray-eee;
    border-right: 1px solid $gray-eee;

    &:last-child {
      border-bottom: 0;
    }
  }
}

.fc-theme-standard td:last-child, .fc-theme-standard td:last-child {
  border-bottom: 1px solid $gray-eee;
}

.fc-theme-standard .fc-scrollgrid {
  border: 0;
}

.fc-scrollgrid-sync-table tbody tr:last-child td {
  border-bottom: 0;
}

.fc-theme-standard .fc-col-header-cell {
  background-color: $gray-222;
  color: $white;
  font-size: 17px;
  font-weight: 500;
  padding: 10.75px 0;
}

.fc .fc-toolbar.fc-header-toolbar {
  margin-bottom: 10px;
}

.fc .fc-daygrid-day-top {
  flex-direction: row;
  justify-content: flex-start;
  padding: 2px 0 0 7px;
}

.fc .fc-daygrid-day-number {
  padding: 0;
  color: $gray-444;
  font-size: 13px;
}

.fc-day-sat .fc-daygrid-day-number {
  color: $point;
}

.fc-day-sun .fc-daygrid-day-number {
  color: #dc2347;
}

