.fc .fc-toolbar-title {
  font-size: 24px;
  line-height: 1.5;
  letter-spacing: -0.96px;
  color: $gray-222;
}

.fc-event-title::before {
  //content: "";
  //margin-right: 3px;
}

.fc-day-today {
  background-color: #f0f2f5 !important;
}

.fc .fc-bg-event {
  opacity: 1;
}

.fc .fc-button {
  border-radius: 2px;
  border: solid 1px $gray-ccc;
  background-color: $white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 30px;
  height: 30px;
  color: $gray-444;

  &:hover {
    border-color: #003675;
    background-color: #003675;
    color: #fff;
  }
}

