.custom-event .fc-event-title {
  color: #666 !important;
  font-size: 13px;
  flex-wrap: wrap;
  text-decoration: underline;
  text-underline-offset: 3px;
  display: flex;
  align-items: center;
}

//.custom-event .fc-event-title::before {
//  content: "";
//  display: inline-block;
//}

.fc-day-today {
  background-color: transparent !important;
}

.fc .is-holiday .fc-daygrid-day-number {
  color: #dc2347;
}

.fc-daygrid-day-events {
  display: flex;
  flex-wrap: wrap;
  padding: 0 5px;
}

.fc-daygrid-event.circle {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  border: 0;

  &.circle-green {
    background-color: #228738;
  }

  &.circle-blue {
    background-color: #0b78cb;
  }

  &.circle-red {
    background-color: #de3412;
  }
}

@media (max-width: $breakpoint-medium) {
  .custom-event .fc-event-title {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  .custom-event .fc-event-title::before {
    content: none;
  }
}