/*
  Pagination
*/

@use "variables" as *;

$pagination-gap: 8px;
$pagination-min-width: 36px;
$pagination-height: 36px;
$pagination-line-height: 36px;
$pagination-font-size: 17px;
$pagination-letter-spacing: -0.75px;
$pagination-color: $gray-444;
$pagination-active-color: #003675;
$pagination-hover-color: $gray-222;
$pagination-border-color: transparent;
$pagination-hover-border-color: transparent;
$pagination-padding: 0 11px;
$pagination-radius: 4px;

.pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
  padding-top: 30px;
  border-top: 1px solid $gray-eee;

  .page-item {
    .page-link {
      display: flex;
      gap: 5px;
      justify-content: center;
      align-items: center;
      min-width: $pagination-min-width;
      height: $pagination-height;
      text-align: center;
      font-size: $pagination-font-size;
      line-height: $pagination-line-height;
      letter-spacing: $pagination-letter-spacing;
      color: $pagination-color;
      padding: $pagination-padding;
      border: 1px solid $pagination-border-color;
      border-radius: $pagination-radius;
      transition: background-color .2s, border .2s;

      &:hover {
        border-color: $pagination-hover-border-color;
      }

      &:focus-visible {
        outline-color: #256ef4;
        outline-offset: -1px;
      }
    }

    &.active {
      .page-link {
        font-weight: bold;
        color: $white;
        background-color: $pagination-active-color;
        border-color: $pagination-active-color;
      }
    }

    &.disabled {
      .page-link {
        color: #8a949e;
        cursor: default;
        border-color: transparent;
      }

      .ico {
        opacity: .6;
      }
    }

    &.prev, &.first, &.last, &.next {
      .page-link {
        font-size: 15px;
        padding: 0;
      }

      &.disabled {
        .page-link {
          &:hover {
            background-color: transparent;
          }
        }
      }
    }
  }
}