/*
  Layout
*/

@use "variables" as *;
@use "config" as *;

/* layout ------------------------------------------------- */
.wrap {
  position: relative;
  overflow: hidden;
  //min-height: calc(100dvh - 312px - 50px - 80px);
}

.container {
  min-height: 800px;
  margin: 0 auto;
  max-width: none !important;
  padding-top: calc(102px + 32px);
}

.inner {
  max-width: $contents-size;
  margin: 0 auto;
}

@media (max-width: $breakpoint-xlarge) {
  .container {
    padding-top: calc(66px + 32px);
  }

  .inner {
    padding: 0 15px;
  }
}

@media (max-width: $breakpoint-medium) {
  .wrap {
    min-height: auto;
  }
}
/* -------------------------------------------------------- */

/* header ------------------------------------------------- */
.skip-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 500;

  a {
    display: block;
    width: 100%;
    height: 0;
    background-color: #555;
    text-align: center;
    color: $white;
    font-size: 0;
    padding: 0;

    &:focus {
      line-height: 30px;
      font-size: 16px;
      height: 30px;
      outline: none;
    }
  }
}

.mast-head {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: $bg-hover;
  z-index: 10;

  .inner {
    display: flex;
    position: relative;
    gap: 4px 16px;
    min-height: 32px;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 4px 0;
  }

  .nuri-txt {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
    gap: 8px;
    font-size: 15px;
    white-space: nowrap;

    &::before {
      content: '';
      display: inline-flex;
      width: 24px;
      height: 24px;
      background-image: url(../images/common/ico-flag.svg);
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}

.header {
  position: fixed;
  top: 32px;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #d8d8d8;
  background-color: $white;
  z-index: 10;

  .utility {
    height: 40px;
    display: flex;
    justify-content: flex-end;

    .list {
      display: flex;
      align-items: center;

      .item {
        display: flex;
        align-items: center;

        > .item-link {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          font-size: 15px;
          gap: 2px;
          padding: 2px 5px;
          transition: 0.4s ease-in-out;
          border-radius: 6px;
        }

        + .item {
          &::before {
            content: '';
            display: inline-flex;
            width: 1px;
            height: 16px;
            background-color: #d8d8d8;
            margin: 0 8px;
          }
        }
      }
    }
  }

  .btn-menu {
    position: relative;
    width: 20px;
    height: 20px;
    padding-right: 75px;

    span {
      position: absolute;
      display: block;
      width: 16px;
      height: 2px;
      transition: all 0.2s;
      background-color: $gray-222;
      left: 2px;

      &:nth-child(1) {
        top: 2px;
      }

      &:nth-child(2) {
        top: 9px;
      }

      &:nth-child(3) {
        top: 16px;
      }
    }

    em {
      display: inline-flex;
      font-size: 15px;
      font-weight: 500;
      line-height: 20px;
      color: $gray-444;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  .btn-search {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    font-size: 15px;
    font-weight: 500;
    color: $gray-444;
    gap: 4px;
  }

  .branding {
    display: flex;
    align-items: center;
    padding-bottom: 16px;
  }

  .logo {
    flex-shrink: 0;
    width: 229px;
    height: 48px;

    a {
      display: block;
      width: 100%;
      height: auto;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .main-menu {
    margin-left: auto;

    .gnb-menu {
      display: flex;
      align-items: center;
      gap: 80px;

      .menu-item {
        position: relative;
      }

      .menu-link {
        position: relative;
        transition: .2s;
        font-size: 24px;
        white-space: nowrap;
        font-weight: bold;

        &::after {
          content: '';
          position: absolute;
          width: 0;
          height: 4px;
          bottom: -6px;
          left: 0;
          transition: .2s;
          background-color: transparent;
        }

        &:hover, &[aria-expanded="true"] {
          color: #256ef4;

          &::after {
            width: 100%;
            background-color: #256ef4;
          }
        }
      }
    }

    .sub-menu {
      position: fixed;
      top: calc(32px + 105px);
      left: 0;
      width: 100%;
      height: 55px;
      background-color: $white;
      z-index: 2;
      border-bottom: 1px solid #d8d8d8;

      .depth2 {
        text-align: center;
        display: flex;
        height: 100%;
        max-width: $breakpoint-xlarge;
        margin: 0 auto;
        justify-content: center;
        gap: 0 30px;

        .depth2-item {
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;

          &::after {
            content: '';
            position: absolute;
            right: -15px;
            top: 50%;
            transform: translateY(-50%) rotate(-15deg);
            width: 1px;
            height: 18px;
            background-color: #cdd1d5;
          }

          &.is-active {
            .depth2-link {
              &:not(:only-child) {
                &::after {
                  -webkit-mask: url(../images/common/ico-gnb-minus.svg) no-repeat center / contain;
                  mask: url(../images/common/ico-gnb-minus.svg) no-repeat center / contain;
                }

                &:hover {
                  &::after {
                    background-color: $point;
                  }
                }
                &[aria-current="page"] {
                  &:hover {
                    &::after {
                      background-color: $white;
                    }
                  }
                }
              }
            }
          }

          &:last-child {
            &::after {
              content: none;
            }
          }

          .depth2-link {
            position: relative;
            padding: 0 15px;
            height: 34px;
            line-height: 34px;
            font-weight: bold;
            color: $gray-222;
            font-size: 17px;
            letter-spacing: -.85px;
            align-items: center;
            white-space: nowrap;
            border-radius: 17px;
            background-color: transparent;
            transition: .2s;

            &:only-child {
              &:hover {
                color: #256ef4;
              }
            }

            &:not(:only-child) {
              flex: none;

              &::after {
                content: '';
                display: inline-flex;
                width: 8px;
                height: 8px;
                background-size: cover;
                background-repeat: no-repeat;
                background-color: $gray-222;
                -webkit-mask: url(../images/common/ico-gnb-plus.svg) no-repeat center / contain;
                mask: url(../images/common/ico-gnb-plus.svg) no-repeat center / contain;
                margin-left: 6px;
                transform: translateY(-2px);
                transition: .2s;
              }

              &:hover {
                color: #256ef4;
                font-weight: bold;

                &::after {
                  background-color: $point;
                }
              }

              &[aria-current="page"] {
                &::after {
                  background-color: $white;
                }
              }
            }

            &[aria-current="page"] {
              background-color: #256ef4;
              color: $white;

              &:only-child {
                flex: none;

                &:hover {
                  color: $white;
                }
              }

              &:not(:only-child) {
                &:hover {
                  color: $white;

                  &::after {
                    background-color: $white;
                  }
                }
              }
            }
          }
        }
      }

      .depth3-menu {
        flex: 1 1 auto;
        min-width: 0;
        padding: 0 0 0 15px;

        .depth3 {
          display: flex;
          flex-wrap: nowrap;
          overflow-x: auto;
          gap: 2px;
          -webkit-overflow-scrolling: touch;

          a {
            display: inline-flex;
            white-space: nowrap;
            padding: 0 15px;
            height: 34px;
            line-height: 34px;
            font-size: 17px;
            font-weight: 500;
            border-radius: 17px;
            background-color: transparent;
            color: $gray-444;
            transition: .2s;

            &:focus, &:focus-visible {
              outline-offset: -2px;
            }

            &:hover {
              color: #256ef4;
              font-weight: bold;
            }

            &[aria-current="page"] {
              background-color: #256ef4;
              font-weight: bold;
              color: $white;
            }
          }
        }
      }
    }
  }

  .actions {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-left: 130px;

    .action-item {
      display: flex;
      justify-content: center;
      align-items: center;
      transition: .2s;
      border-radius: 6px;
      padding: 5px;

      &:hover {
        background-color: #EDF1F5;
      }

      &:active {
        background-color: #CDD7E4;
      }
    }

    .txt-link {
      font-size: 16px;
      font-weight: 500;
      color: $gray-444;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .mast-head {
    .inner {
      padding: 4px 15px;
    }

    .nuri-txt {
      gap: 4px;
      font-size: 15px;
    }
  }

  .header {
    .utility {
      display: none;
    }

    .branding {
      height: 65px;
      padding-bottom: 0;
      gap: 10px;
    }

    .logo {
        width: 172px;
        height: 32.3px;
        margin-right: 0;
      }

    .main-menu {
      position: fixed;
      top: calc(65px + 32px);
      left: 0;
      width: 100%;
      //display: none;

      .gnb-menu {
        .menu-item {
          &.is-pinned {
            .sub-menu {
              display: block;
              top: calc(65px + 33px);
              overflow-x: auto;
              padding: 0 15px;

              .depth2 {
                display: block;
                width: fit-content;
                white-space: nowrap;
                padding-top: 11px;

                .depth2-item {
                  display: inline-block;

                  + .depth2-item {
                    margin-left: 30px;
                  }

                  &:last-child {
                    margin-right: 15px;
                  }

                  .depth2-link {
                    display: inline-block;
                  }
                }
              }

              .depth3-menu {
                display: inline-block;
              }
            }
          }
        }

        .menu-link {
          display: none;
        }
      }
    }

    .actions {
      margin-left: auto;
      gap: 10px;
    }

    .btn-search {
      width: auto;
      height: auto;
      display: flex;
      align-items: center;
      gap: 5px;

      .ico {
        flex-shrink: 0;
      }

      em {
        display: inline-flex;
        font-size: 15px;
        line-height: 20px;
        font-weight: 500;
        white-space: nowrap;
        color: $gray-444;
      }
    }

    .action-item.show-mo {
      display: flex !important;
    }

    .btn-menu {
      width: auto;
    }
  }
}

@media (max-width: $breakpoint-small) {
  .header {
    .actions {
      gap: 10px;
    }

    .btn-search {
      em {
        display: none !important;
      }
    }

    .btn-menu {
      padding-right: 20px;

      em {
        display: none;
      }
    }
  }
}
/* -------------------------------------------------------- */

/* footer ------------------------------------------------- */
.footer {
  margin-top: 100px;

  a {
    &:focus {
      outline: none;
      border: 0;
    }

    &:focus-visible {
      outline: 2px solid $black;
      outline-offset: 2px;
    }
  }

  .footer-dropdown {
    position: relative;
    height: fit-content;

    .btn-select {
      display: flex;
      width: 240px;
      height: 40px;
      font-weight: 500;
      font-size: 16px;
      letter-spacing: -.85px;
      border: 1px solid #464C53;
      border-radius: 3px;
      color: #464C53;
      padding: 9px 15px;
      align-items: center;
      justify-content: space-between;

      &::after {
        content: '';
        display: block;
        width: 14px;
        height: 9px;
        background: url(../images/common/footer-select.svg) no-repeat center;
        box-sizing: border-box;
        -webkit-text-size-adjust: 100%;
        -webkit-font-smoothing: antialiased;
      }
    }

    .dropdown-list {
      position: absolute;
      right: 0;
      bottom: 0;
      width: 100%;
      max-height: 200px;
      padding: 10px 15px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0 -2px 10px rgba(0, 0, 0, .2);
      overflow-y: auto;
      transition: 0.3s;
      opacity: 0;
      visibility: hidden;

      a {
        display: block;
        padding: 8px 10px;
        font-weight: bold;
        font-size: 15px;
        border-bottom: 1px solid $gray-eee;
        transition: color .2s;

        &:hover {
          color: #2462d6;
        }
      }

      li:last-child {
        a {
          border-bottom: 0;
        }
      }
    }

    .btn-select-close {
      display: none;
      width: 240px;
      height: 40px;
      font-weight: 500;
      font-size: 15px;
      letter-spacing: -.85px;
      border: 2px solid #697077;
      padding: 10px 16px;
      align-items: center;
      justify-content: space-between;
    }

    &.active {
      .btn-select {
        display: none;
      }

      .dropdown-list {
        bottom: 40px;
        opacity: 1;
        visibility: visible;
      }

      .btn-select-close {
        display: flex;
        color: #2462d6;
        border-color: #2462d6;

        &::after {
          content: '';
          box-sizing: border-box;
          -webkit-text-size-adjust: 100%;
          -webkit-font-smoothing: antialiased;
          display: block;
          width: 12px;
          height: 7px;
          background: url(../images/common/select-close.png) no-repeat center;
        }
      }
    }
  }

  .logo {
    width: 247px;
    margin-bottom: 37px;

    a {
      display: block;
      width: 100%;
      height: 100%;

      img {
        display: block;
        width: 100%;
      }
    }
  }

  .footer-info {
    background-image: url(../images/common/bg-footer.png);
    background-color: #003675;
    background-size: 1920px 120px;
    background-repeat: no-repeat;
    background-position: right center;
    color: $white;
    height: 120px;

    .inner {
      display: flex;
      align-items: center;
      height: 100%;
    }

    .key-info {
      display: flex;
      flex-wrap: wrap;
      gap: 10px 35px;
      align-items: center;

      .call {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 10px;
        font-weight: bold;
        font-size: 32px;
        letter-spacing: -1.44px;

        a {
          margin-left: 5px;
        }
      }

      .time {
        font-size: 24px;
        letter-spacing: -1.08px;
        font-weight: 400;
      }
    }
  }

  .footer-links {
    padding: 40px 0 50px;
    background-color: #e4e4e4;

    .link-content {
      display: flex;
      justify-content: space-between;
    }

    .info-links {
      display: flex;
      flex-wrap: wrap;
      gap: 20px 40px;
      margin-bottom: 20px;

      .link {
        position: relative;
        font-size: 15px;
        font-weight: bold;
        color: #464c53;
        //line-height: 24px;
        //letter-spacing: 0.6px;

        + .link {
          &::before {
            content: '';
            position: absolute;
            left: -20px;
            top: 50%;
            transform: translateY(-50%);
            width: 2px;
            height: 13px;
            background-color: #bebebe;
          }
        }

        &.link-highlight {
          font-weight: bold;
          color: #003675;
        }
      }
    }

    .address {
      font-size: 14px;
      color: #6D7882;
      margin-bottom: 2px;
    }

    .copyright {
      font-size: 14px;
      color: #6D7882;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .footer {
    margin-top: 70px;

    .logo {
      width: 172px;
      height: 32px;
      margin: 0 auto 25px;
    }

    .footer-info {
      height: auto;
      padding: 20px 0;
      min-height: 120px;

      .inner {
        flex-direction: column;
        border-bottom: 0;
        padding-bottom: 0;
      }

      .key-info {
        flex-direction: column;
        height: 100%;
        justify-content: center;
        align-items: center;
        gap: 5px;
        text-align: center;
      }
    }

    .footer-links {
      padding-top: 26px;

      .link-content {
        flex-direction: column;
      }

      .info-links {
        justify-content: center;
        margin-bottom: 30px;
        gap: 10px 40px;
      }

      .address {
        text-align: center;
      }

      .copyright {
        text-align: center;
      }
    }

    .footer-dropdown {
      margin: 30px auto 0;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .footer {
    .footer-info {
      .key-info {
        .call {
          font-size: 28px;
        }

        .time {
          font-size: 17px;
        }
      }
    }
  }
}
/* -------------------------------------------------------- */

/* family site -------------------------------------------- */
.family-site {
  padding: 10px 0;
  border-top: 1px solid #dfdfdf;
  margin-top: 30px;

  .swiper {
    max-width: 1200px;
    margin: 0 auto;

    .swiper-slide {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 60px;

      a {
        display: flex;
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
      }
    }
  }

  .btn-prev, .btn-next {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;

    &.swiper-button-disabled {
      opacity: 0.3;
    }
  }

  .btn-prev {
    left: 15px;
  }

  .btn-next {
    right: 15px;
  }
}

@media (min-width: $breakpoint-xlarge) {
  .family-site {
    .btn-prev, .btn-next {
      visibility: hidden;
      cursor: default;
    }
  }
}
/* -------------------------------------------------------- */

/* etc ---------------------------------------------------- */
.btn-chatbot {
  position: fixed;
  bottom: 22%;
  transform: translateY(-50%);
  right: 20px;
  width: 72px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  text-align: center;

  .txt {
    font-size: 15px;
    font-weight: bold;
    line-height: 14px;
    letter-spacing: -0.7px;
    text-align: center;
    color: #0e75dc;
    margin-top: 8px;
  }
}

@media (max-width: $breakpoint-medium) {
  .btn-chatbot {
    bottom: 3%;

    .txt {
      display: none;
    }
  }
}
