/*
  Breadcrumb
*/

@use "variables" as *;

.breadcrumb {
  max-width: $breakpoint-xlarge;
  margin: 20px auto 30px;

  .breadcrumb-list {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .breadcrumb-item {
    position: relative;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: -0.7px;
    color: $gray-444;
    //text-transform: uppercase;

    a {
      display: inline-flex;
      gap: 5px;
      align-items: center;
      vertical-align: middle;
      text-decoration: underline;
      padding: 0 4px;
      border-radius: 6px;
      text-underline-offset: 3px;

      &:hover {
        background-color: $bg-hover;
      }

      &:active {
        background-color: $bg-active;
      }
    }

    + .breadcrumb-item {
      margin-left: 30px;

      &::before {
        content: '';
        position: absolute;
        left: -20px;
        top: 5px;
        display: inline-block;
        width: 7px;
        height: 10px;
        background-image: url(../images/common/ico-breadcrumb.svg);
        background-size: cover;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .breadcrumb {
    padding: 0 15px;
  }
}