/*
  Dropdown
*/
@use "variables" as *;

.drop-wrap {
  display: inline-flex;
  position: relative;

  .drop-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    gap: 2px;
    padding: 2px 5px;
    transition: .4s ease-in-out;
    border-radius: 6px;

    &:hover {
      background-color: #edf1f5;
    }
  }

  &.active {
    .drop-btn {
      .ico-toggle {
        transform: rotate(180deg);
      }
    }
  }

  .drop-menu {
    position: absolute;
    top: 100%;
    left: 50%;
    z-index: 6;
    min-width: 150px;
    padding: 8px;
    margin-top: 16px;
    border-radius: 8px;
    border: 1px solid #d8d8d8;
    background-color: $white;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.12);
    transform: translateX(-50%);

    &::before {
      content: '';
      display: inline-flex;
      position: absolute;
      left: 50%;
      bottom: 100%;
      width: 20px;
      height: 12px;
      background-image: url(../images/common/ico-top-arrow.svg);
      transform: translateX(-50%);
    }

    .drop-in {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: column;
      gap: 8px;

      .drop-list {
        width: 100%;

        .item-link {
          display: flex;
          width: 100%;
          align-items: center;
          justify-content: flex-start;
          flex-direction: row;
          gap: 7px;
          font-size: 13px;
          height: 35px;
          padding: 0 16px;
          border-radius: 4px;
          color: #1D1D1D;

          &:hover {
            color: #003675;
            font-weight: 600;
            background-color: #EDF1F5;
          }

          &:active {
            color: #003675;
            font-weight: 600;
            background-color: #CDD7E4;
          }

          &.on {
            font-weight: 600;
            color: #003675;
            background-color: #EDF1F5;
          }
        }
      }
    }

    .ex-zoom {
      display: inline-flex;
      padding: 5px 4px;
      font-weight: 400;
      border: 1px solid #D8D8D8;
      border-radius: 5px;
      text-align: center;
    }

    .on > .ex-zoom {
      background-color: #003675;
      color: $white;
    }

    [data-zoom="0.9"] {
      .ex-zoom {
        font-size: 15px;
        line-height: 9px;
      }
    }

    [data-zoom="1"] {
      .ex-zoom {
        font-size: 17px;
        line-height: 12px;
      }
    }

    [data-zoom="1.1"] {
      .ex-zoom {
        font-size: 19px;
        line-height: 13px;
      }
    }

    [data-zoom="1.2"] {
      .ex-zoom {
        font-size: 21px;
        line-height: 15px;
      }
    }

    [data-zoom="1.3"] {
      .ex-zoom {
        font-size: 25px;
        line-height: 18px;
      }
    }
  }
}