/*
  Table
*/

@use "variables" as *;

caption {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

table {
  table-layout: fixed;
  max-width: none;
  width: auto;
  min-width: 100%;
  border-collapse: collapse;
}

.table {
  table-layout: fixed;
  max-width: none;
  width: auto;
  min-width: 100%;
  border-collapse: collapse;

  &.v-table {
    tbody {
      th, td {
        border-color: #d7d9db;
      }
    }
  }

  th, td {
    vertical-align: middle;
  }

  thead {
    th {
      padding: 11.5px 16px;
      background-color: #eef2f7;
      border-bottom: 1px solid #d7d9db;
      font-weight: bold;
      font-size: 15px;
      text-align: left;

      &:first-child {
        border-radius: 4px 0 0 0;
      }

      &:last-child {
        border-radius: 0 4px 0 0;
      }
    }
  }

  colgroup + tbody {
    tr {
      border-top: 1px solid #d7d9db;
    }
  }

  .col-title {
    padding: 8px 16px;
    background-color: #eef2f7;
    border-bottom: 1px solid #d7d9db;
    font-weight: bold;
    font-size: 15px;
    text-align: left;
  }

  tbody {
    th, td {
      padding: 13.25px 16px;
      border-bottom: 1px solid $gray-eee;
      font-size: 15px;
      font-weight: 400;
      text-align: left;
      color: $gray-444;
    }
  }

  .text-link {
    &:hover {
      text-decoration: underline;
      text-underline-offset: 4px;
    }
  }

  .ellipse {
    display: block;
    max-width: 100%;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
  }

  .no-data {
    display: table-cell;
    padding: 100px 16px;

    .txt {
      font-size: 16px;
      line-height: 16px;
      letter-spacing: -0.8px;
      color: $gray-666;
      text-align: center;
    }
  }
}

.table-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;

  .title {
    font-size: 20px;
    font-weight: bold;
  }
}

.table-container {
  overflow-x: auto;

  .table {
    min-width: 1200px;
  }
}

@media (max-width: $breakpoint-medium) {
  .table {
    &.table-data {
      min-width: auto;
      display: flex;
      flex-direction: column;
      border-top: 2px solid $gray-222;

      thead, thead th {
        display: none;
      }

      tbody {
        tr {
          display: flex;
          flex-direction: column;
          width: 100%;
          padding: 15px 0;
          border-bottom: 1px solid $gray-eee;
        }

        td {
          position: relative;
          width: 100%;
          padding: 4px 0 4px 56px;
          border-bottom: 0;
          text-align: left !important;
          vertical-align: middle;

          &:empty {
            display: none;
          }

          &:not(.index, .name) {
            &::before {
              content: attr(data-label) '';
              position: absolute;
              top: 4px;
              left: 0;
              display: inline-block;
              width: 56px;
              font-weight: 700;
            }
          }
        }
      }

