/*
  Icon
*/
@use "variables" as *;

.ico {
  display: inline-block;
  background-size: cover;
  background-repeat: no-repeat;
}

.ico-search {
  width: 20px;
  height: 20px;
  background-image: url(../images/common/ico-search.svg);
}

.ico-toggle {
  width: 16px;
  height: 16px;
  background-image: url(../images/common/ico-toggle.svg);
}

.ico-go {
  width: 16px;
  height: 16px;
  background-image: url(../images/common/ico-go.svg);
}

.ico-plus {
  width: 10px;
  height: 10px;
  background-image: url(../images/common/ico-plus.svg);
}

.ico-slider-prev-sm {
  width: 7px;
  height: 12px;
  background-image: url(../images/main/ico-slider-prev-sm.png);
}

.ico-slider-next-sm {
  width: 7px;
  height: 12px;
  background-image: url(../images/main/ico-slider-next-sm.png);
}

.ico-slider-paused {
  width: 8px;
  height: 10px;
  background-image: url(../images/main/ico-slider-paused.png);
}

.ico-slider-paused-white {
  width: 10px;
  height: 12px;
  background-image: url(../images/main/ico-slider-paused-white.png);
}

.ico-slider-prev {
  width: 10px;
  height: 18px;
  background-image: url(../images/main/ico-slider-prev.png);
}

.ico-slider-next {
  width: 10px;
  height: 18px;
  background-image: url(../images/main/ico-slider-next.png);
}

.ico-search-main {
