  background-image: url(../images/common/ico-verified.png);
}

.ico-external {
  width: 14px;
  height: 14px;
  background-color: currentColor;
  -webkit-mask: url(../images/common/ico-external-black.svg) no-repeat center / contain;
  mask: url(../images/common/ico-external-black.svg) no-repeat center / contain;
}

.ico-go-blue {
  width: 24px;
  height: 24px;
  background-color: currentColor;
  -webkit-mask: url(../images/common/ico-go-blue.svg) no-repeat center / contain;
  mask: url(../images/common/ico-go-blue.svg) no-repeat center / contain;
}

*:hover > .ico-external {
  background-color: $white;
}

.ico-paging-prev {
