/*
  Popup
*/

@use "variables" as *;
@use "config" as *;

$popup-sm-width: 400px;
$popup-width: 560px;
$popup-lg-width: 760px;
$popup-xl-width: 1000px;

.dim {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: .2s;
  z-index: 100;
  background-color: #000000bf;
}

.popup-wrap {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: visibility 0.15s 0.3s, z-index 0s 0.3s, opacity 0.2s;
  font-style: normal;
  text-align: left;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  white-space: normal;
  word-spacing: normal;
  line-break: auto;
  z-index: 101;
}

.popup {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  position: relative;
  z-index: 101;
  width: $popup-width;
  height: 100%;
  min-height: 264px;
  margin: 0 auto;

  .popup-content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    position: relative;
    width: 100%;
    max-height: 80%;
    background-color: $white;
    border-radius: 12px;
    border: 1px solid #b1b8be;
    outline-color: transparent;
    transition: outline-color 0.1s;
    box-shadow: 0 0 2px 0 #00000014, 0 16px 24px 0 #0000001f;
  }

  .popup-head {
    width: 100%;
    padding-top: 56px;
    padding-inline: 40px;

    .title {
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 1;
      font-size: 24px;
      font-weight: bold;
    }

    .btn-popup-close {
      position: absolute;
      top: 24px;
      right: 24px;
      z-index: 1;
    }
  }

  .popup-body {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    position: relative;
    overflow-y: auto;
    width: 100%;
    padding: 16px 40px 8px;

    .content {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: column;
      gap: 16px;
    }

    &:last-child {
      padding-bottom: 40px;
    }
  }

  .popup-footer {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
    gap: 8px;
    padding: 16px 40px 40px;
  }

  &.popup-sm {
    width: $popup-sm-width;
  }

  &.popup-lg {
    width: $popup-lg-width;
  }

  &.popup-xl {
    width: $popup-xl-width;
  }
}

@media (max-width: $breakpoint-xlarge) {
  .popup {
    .popup-head {
      padding-inline: 20px;
    }

    .popup-body {
      padding: 16px 20px 8px;
    }

    &.popup-xl {
      width: calc(100% - 40px);
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .popup {
    width: calc(100% - 40px);

    &.popup-lg {
      width: calc(100% - 40px);
    }
  }
}

@media (max-width: 560px) {
  .popup {
    &.popup-sm {
      width: calc(100% - 40px);
    }
  }
}

//  -  
.popup-video-content {
  display: flex;
  width: 100%;
  gap: 10px;

  .video-content {
    flex: 1;
    min-width: 0;
    width: 100%;

    .video-area {
      aspect-ratio: 680 / 380;
      border-radius: 10px;
      overflow: hidden;
      margin-bottom: 20px;
    }

    .record-info {
      margin-bottom: 20px;
    }

    .video-info-title {
      font-size: 19px;
      font-weight: bold;
      letter-spacing: -0.48px;
      margin-bottom: 5px;
    }

    .video-info-content {
      font-size: 17px;
      letter-spacing: -0.85px;
      color: $gray-444;
      border-radius: 10px;
      background-color: #f9f9f9;
      //display: -webkit-box;
      //overflow: hidden;
      //text-overflow: ellipsis;
      //-webkit-box-orient: vertical;
      //-webkit-line-clamp: 3;
      padding: 13px 15px;
      max-height: 102.5px;
      overflow-y: auto;
    }
  }

  .result-group {
    .record-info {
      .record-info-item {
        font-size: 15px;

        + .record-info-item {
          &::before {
            background-color: $gray-ccc;
            top: 5px;
          }
        }
      }
    }
  }

  .ai-subtitle {
    width: 230px;
    border-radius: 10px;
    border: solid 1px #dfdfdf;
    background-color: #fff;
    padding: 16px;
    max-height: 580px;

    .ai-sub-title-content {
      max-height: calc(580px - 25.5px - 10px - 32px);
      overflow-y: auto;
    }

    .ai-subtitle-title {
      font-size: 17px;
      font-weight: bold;
      letter-spacing: -0.43px;
      margin-bottom: 10px;
    }

    .ai-sub-title-content {
      font-size: 15px;
      letter-spacing: -1.13px;
      color: $gray-444;
    }
  }
}

@media (max-width: 1000px) {
  .popup-video-content {
    flex-direction: column;

    .ai-subtitle {
      width: 100%;
    }
  }
}

//  -  
.popup-total-search {
  .popup-content {
    background-color: #eef2f7 !important;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    border: solid 1px #b1b8be;
  }

  .popup-head {
    display: flex;
    padding-top: 87px;
    padding-inline: 140px;

    .btn-popup-close {
      top: 55px;
      right: 55px;
    }

    .tip-link {
      display: inline-flex;
      margin-left: auto;
      font-size: 15px;
      gap: 4px;
      align-items: center;
      color: $gray-444;

      .ico {
        transform: translateY(-1px);
      }
    }
  }

  .popup-body {
    padding: 13px 140px 105px;

    &:last-child {
      padding-bottom: 105px;
    }
  }
}

.popup-search-content {
  width: 100%;

  .search-global {
    width: 100%;
    box-shadow: 0 3px 10px 0 rgba(36, 98, 214, 0.2);
    border-radius: 10px;
    margin-bottom: 0;
  }

  .sch-info-list {
    display: flex;
    width: 100%;
    margin-top: 40px;
    padding-top: 34px;
    border-top: 1px solid #dfe6ef;

    .sch-info-item {
      display: inline-flex;
      align-items: flex-start;
      justify-content: flex-start;
      flex-direction: column;
      flex: 1;
      width: 50%;
      padding: 0 24px;
      min-height: 340px;

      .btn-del {
        width: 16px;
        height: 16px;
      }
      
      a {
        &:hover {
          text-decoration: underline;
          text-underline-offset: 4px;
        }
      }

      &.word-popular {
        counter-reset: item;

        li {
          gap: 10px;
          counter-increment: item;

          &::before {
            display: inline-flex;
            content: counter(item);
            width: 18px;
          }
        }
      }

      &.word-lately {
        border-left: 1px solid #dfe6ef;

        li {
          justify-content: space-between;
        }
      }

      .tit {
        width: 100%;
        margin-bottom: 17px;
        font-size: 19px;
        font-weight: bold;
      }

      .list {
        display: flex;
        align-items: normal;
        justify-content: flex-start;
        flex-direction: column;
        gap: 6px;
        width: 100%;

        li {
          display: flex;
          align-items: center;
          //justify-content: space-between;
          flex-direction: row;
        }
      }

      .ico-del {
        width: 16px;
        height: 16px;
      }

      .ranking-txt {
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
      }

      .ranking-state {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        flex-shrink: 0;
        gap: 2px;
        font-size: 15px;
        width: 43px;
        margin-left: auto;

        &::before {
          content: '';
          display: inline-flex;
        }

        &.down {
          color: #096ab3;

          &::before {
            width: 0;
            height: 0;
            border-top: 6px solid #0b78cb;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
          }
        }

        &.same {
          &::before {
            width: 6px;
            height: 2px;
            background-color: #1e2124;
          }
        }

        &.up {
          color: #ab2b36;

          &::before {
            width: 0;
            height: 0;
            border-bottom: 6px solid #d63d4a;
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
          }
        }
      }

      .btn-del-all {
        display: flex;
        margin-top: auto;
        align-items: center;
        gap: 5px;
        font-size: 15px;
        color: $gray-444;
        letter-spacing: -.38px;

        .ico-search-arrow {
          width: 18px;
          height: 18px;
          transform: translateY(-2px);
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .popup-total-search {
    .popup-head {
      flex-direction: column;
      padding-top: 40px;
      padding-inline: 40px;
      gap: 5px;

      .btn-popup-close {
        top: 20px;
        right: 20px;
      }
    }

    .popup-body {
      padding: 13px 40px 40px;

      &:last-child {
        padding-bottom: 40px;
      }
    }
  }

  .popup-search-content {
    .sch-info-list {
      flex-direction: column;
      gap: 30px;
      padding-top: 20px;
      margin-top: 20px;

      .sch-info-item {
        width: 100%;
        padding: 0;
        min-height: auto;

        .list {
          gap: 10px;
        }

        .tit {
          margin-bottom: 20px;
        }

        &.word-lately {
          border-top: 1px solid #d6e0eb;
          border-left: 0;
          padding-top: 30px;
        }

        .btn-del-all {
          padding: 0;
          margin-top: 20px;
        }
      }
    }
  }
}