/*
  Badge
*/

@use "variables" as *;

.badge-wrap {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
}

.badge {
  display: inline-flex;
  justify-content: center;
  gap: 3px;
  align-items: center;
  height: 20px;
  line-height: 18px;
  border-radius: 2px;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  padding: 0 6px;
  border: solid 1px #9dd7d7;
  background-color: $white;
  color: #045d5d;

  &.badge-green-filled {
    border-color: #d6f5d6;
    background-color: #d6f5d6;
    color: #1e7b1e;
  }

  &.badge-blue-filled {
    border-color: #e7f4fe;
    background-color: #e7f4fe;
    color: #0C6CB4;
  }

  &.badge-red-filled {
    border-color: #fdefec;
    background-color: #fdefec;
    color: #be2f13;
  }

  &.badge-green-filled {
    border-color: #d6f5d6;
    background-color: #eaf6ec;
    color: #1e7b1e;
  }

  &.badge-green {
    border-color: #9dd7d7;
    color: #045d5d;
  }

  &.badge-blue {
    border-color: #5fb5f7;
    color: #096ab3;
  }

  &.badge-pink {
    border-color: #ffc6ec;
    color: #a14482;
  }

  &.badge-red {
    border-color: #ffb599;
    color: #d53209;
  }

  &.badge-yellow {
    border-color: #f1cf7a;
    color: #8b6914;
  }
}