/*
  Common
*/

@use "variables" as *;
@use "config" as *;

@include scrollbar;

@media (max-width: $breakpoint-xlarge) {
  body {
    touch-action: pan-y;
  }
}

ul, ol {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a, button {
  color: inherit;
  background-color: transparent;
}

body.scrOff {
  overflow: hidden;
}

em, i {
  font-style: normal;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

::before, ::after {
  box-sizing: border-box;
}

.color-red {
  color: $red !important;
}

.color-black {
  color: $black !important;
}

.color-point {
  color: $point !important;
}

.color-sub {
  color: $red !important;
}

.color-darkblue {
  color: $dark-blue !important;
}

.color-blue {
  color: $point !important;
}

.color-disabled {
  color: $disabled !important;
}

.color-gray {
  color: $gray-666 !important;
}

.color-caution {
  color: $red !important;
}

.color-white {
  color: $white !important;
}

.color-green {
  color: $green !important;
}

.font-weight-black {
  font-weight: 900 !important;
}

.font-weight-bold {
  font-weight: 700 !important;
}

.font-weight-regular {
  font-weight: 400 !important;
}

.font-weight-medium {
  font-weight: 500 !important;
}

.align-left {
  text-align: left !important;
}

.align-right {
  text-align: right !important;
}

.align-center {
  text-align: center !important;
}

.underline {
  text-decoration: underline;
}

.sr-only {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

.w-100 {
  width: 100% !important;
}

.d-none {
  display: none;
}

.img-error {
  width: 100%;
  display: flex;
  justify-content: center;
}

.d-flex {
  display: flex !important;
}

.gap-5 {
  gap: 5px !important;
}

.gap-10 {
  gap: 10px !important;
}

.pt-0 {
  padding-top: 0 !important;
}

.p-0 {
  padding: 0 !important;
}

.mt-30 {
  margin-top: 30px !important;
}

.mt-0 {
  margin-top: 0 !important;
}

.mb-0 {
  margin-bottom: 0 !important;
}

.mb-10 {
  margin-bottom: 10px !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

.mb-30 {
  margin-bottom: 30px !important;
}

.align-items-center {
  align-items: center !important;
}

.justify-content-center {
  justify-content: center !important;
}

.justify-content-start {
  justify-content: flex-start !important;
}

.justify-content-space-between {
  justify-content: space-between !important;
}

.white-space-nowrap {
  white-space: nowrap !important;
}

.contents-info {
  padding: 20px;
  border-radius: 10px;
  border: solid 1px #e7e1d0;
  background-color: #fffcf4;

  .item {
    position: relative;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: -0.75px;
    color: $gray-333;
    padding-left: 21px;
    display: flex;
    flex-wrap: wrap;
    word-break: keep-all;
    gap: 8px 10px;

    .btn-sm {
      margin-top: -3px;
    }

    .ico-tip {
      position: absolute;
      top: 2px;
      left: 0;
    }

    .ico-caution {
      position: absolute;
      top: 1px;
      left: 0;
    }

    + .item {
      margin-top: 12px;
    }
  }

  &.contents-info-gray {
    padding: 10px 20px;
    border: 0;
    background-color: #f6f6f6;
  }

  &.contents-with-title {
    display: flex;
    gap: 20px;

    .item-title {
      font-size: 18px;
      font-weight: bold;
      letter-spacing: -0.9px;
      color: $gray-000;
      flex-shrink: 0;
    }
  }

  &.contents-sm {
    padding: 12px;

    .item {
      line-height: 16px;
    }
  }

  &.contents-center {
    .item {
      justify-content: center;
      padding: 0;

      .ico-tip {
        position: static;
      }
    }
  }
}

.contents-tip {
  padding: 14px 26px;
  border-radius: 5px;
  border: solid 1px #e7e1d0;
  background-color: #fffcf4;

  .item {
    font-size: 15px;
    line-height: 24px;
    color: $gray-666;
  }
}

@media (max-width: $breakpoint-medium) {
  .contents-info {
    .item + .item {
      margin-top: 5px;
    }

    &.contents-with-title {
      flex-direction: column;
      gap: 10px;
    }
  }
}

.error-page {
  text-align: center;
  padding: 0 20px 100px;

  .img {
    max-width: 350px;
    margin: 80px auto 50px;

    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }

    &.img-2 {
      max-width: 387px;
      margin: 80px auto 38px;
    }
  }

  .title {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: -1.5px;
    text-align: center;
    color: $gray-000;
    margin-bottom: 23px;
  }

  .desc {
    font-size: 15px;
    letter-spacing: -0.75px;
    line-height: 24px;
    text-align: center;
    color: $gray-666;
    margin-bottom: 26px;
  }

  .box {
    padding: 13px 20px 16px;
    text-align: center;
    border-radius: 5px;
    background-color: #f9f9f9;
    max-width: 380px;
    margin: 0 auto;

    .txt-lg {
      font-weight: 500;
      font-size: 16px;
      line-height: 26px;
      letter-spacing: -.8px;
      margin-bottom: 4px;
    }

    .txt {
      font-weight: 500;
      color: $point;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: -.9px;
    }
  }
}

.error-popup-content {
  text-align: center;
  overflow: hidden;

  .img {
    width: 47px;
    margin: 0 auto 20px;

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

  .txt {
    font-size: 15px;
    line-height: 24px;
    letter-spacing: -0.75px;
    text-align: center;
    color: $gray-666;
    margin-bottom: 40px;
  }

  .info {
    padding: 20px;
    border-radius: 5px;
    background-color: #f9f9f9;
    margin-bottom: 20px;
    text-align: center;

    .tit {
      font-size: 16px;
      font-weight: 500;
      line-height: 26px;
      letter-spacing: -0.8px;
      text-align: center;
      color: $gray-000;
      margin-bottom: 10px;
    }

    .date {
      font-size: 18px;
      font-weight: 500;
      line-height: 24px;
      letter-spacing: -0.9px;
      color: $point;
    }
  }

  .txt-bottom {
    font-size: 15px;
    line-height: 24px;
    letter-spacing: -0.75px;
    text-align: center;
    color: $gray-666;
    margin-bottom: 28px;
  }

  .txt-bottom-link {
    a {
      display: inline-block;
      text-decoration: underline;
      font-size: 13px;
      font-weight: 300;
      line-height: 16px;
      color: $gray-999;
    }
  }
}

.search-keyword {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;

  .keyword {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    height: 28px;
    padding-left: 15px;
    padding-right: 8px;
    background-color: $point;
    border-radius: 14px;

    .txt {
      font-size: 15px;
      font-weight: 500;
      line-height: 28px;
      letter-spacing: -0.35px;
      text-align: left;
      color: $white;
    }

    .btn-del {
      width: 12px;
      height: 12px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.sorting-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
  margin-bottom: 15px;

  .sorting-item {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 60px;
    height: 30px;
    line-height: 30px;
    padding: 0 18px;
    border-radius: 15px;
    border: 1px solid $gray-999;
    background-color: $white;
    font-size: 15px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: -0.75px;
    text-align: center;
    transition: .2s;

    &.on {
      background-color: $point;
      color: $white;
      font-weight: 500;
      border-color: $point;
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .sorting-list {
    .sorting-item {
      padding: 0 10px;
    }
  }
}

.section-item {
  + .section-item {
    margin-top: 40px;
  }
}

@media (max-width: $breakpoint-medium) {
  .section-item {
    + .section-item {
      margin-top: 30px;
    }
  }

  .nbsp {
    display: none !important;
  }
}

.no-data {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;

  .img {
    display: flex;
    justify-content: center;
    width: 100%;
    align-items: center;
    margin-bottom: 20px;
  }

  .txt {
    font-size: 16px;
    line-height: 16px;
    letter-spacing: -0.8px;
    color: $gray-666;
  }

  &.no-data-white {
    .txt {
      color: $white;
    }
  }
}

.inline-group {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  word-break: keep-all;
  align-items: center;
}

.border-bottom-0 {
  border-bottom: 0 !important;
}

.dot-list {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .dot-list-item {
    position: relative;
    font-size: 17px;
    padding-left: 12px;
    color: $gray-444;

    &::before {
      content: '';
      width: 3px;
      height: 3px;
      background-color: $gray-666;
      position: absolute;
      top: 10px;
      left: 0;
    }
  }
}

.vertical-top {
  vertical-align: top !important;
}

.vertical-center {
  vertical-align: middle !important;
}

.border-bottom-dashed {
  border-bottom-style: dashed !important;
}

.border-top-gray {
  border-top-color: $gray-ddd !important;
}

.border-right-gray {
  border-right: 1px solid $gray-ddd !important;
}

.d-mo-flex {
  display: none !important;
}

.d-pc-flex {
  display: flex !important;
}

@media (max-width: $breakpoint-xlarge) {
  .d-mo-flex {
    display: flex !important;
  }

  .d-pc-flex {
    display: none !important;
  }
}

.show-mo {
  display: none !important;
}

@media (max-width: $breakpoint-xlarge) {
  .show-mo {
    display: block !important;
  }

  .show-pc {
    display: none !important;
  }
}

