.search-detail {
  &-box {
    width: 100%;
    padding: 35px 50px;
    border-radius: 20px;
    border: solid 1px #e3eaf3;
    background-color: #f8fafc;

    .search-bar {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;

      .form-keyword {
        position: relative;
        max-width: 500px;
        border: 2px solid $point;
        border-radius: 6px;
        background-color: $white;
        gap: 0;
        align-items: center;

        .divider {
          width: 1px;
          height: 20px;
          background-color: #dfdfdf;
        }

        .form-select, .form-input {
          height: 46px;
          line-height: 44px;
          font-weight: 500;
          padding: 0 20px;
          border: 0;
          border-radius: 0;
          background-color: transparent;
        }

        .form-select {
          flex: 0 0 120px;

          &:focus {
            outline-offset: -2px;
          }
        }
      }

      .form-submit {
        display: flex;
        gap: 10px;
      }

      .btn-detail {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        gap: 5px;
        height: 50px;
        font-weight: 500;
        line-height: 50px;
        border-radius: 6px;
        box-shadow: 0 3px 5px 0 rgba(36, 98, 214, 0.15);
        transition: .2s;
        
        &.btn-border-blue {
          &:hover {
            background-color: #d2e4ff;
          }
        }

        .ico {
          flex-shrink: 0;
        }

        &:focus {
          outline-offset: 4px;
        }

        &[aria-expanded="true"] {
          .ico-magnify-plus {
            background-image: url(../images/common/ico-magnify-minus.svg);
          }
        }
      }
    }

    .search-option {
      display: flex;

      .label {
        width: 150px;
        font-size: 19px;
        font-weight: bold;
        flex-shrink: 0;
        min-width: 0;
      }

      .option-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        .option-list-item {
          a {
            display: inline-flex;
            height: 34px;
            line-height: 32px;
            padding: 0 15px;
            border-radius: 17px;
            border: solid 1px #a2afc7;
            background-color: $white;
            transition: .2s;
            min-width: 100px;
            justify-content: center;
            font-size: 17px;
            font-weight: 500;
            letter-spacing: -0.85px;
          }

          &.on {
            a {
              color: $white;
              font-weight: 500;
              border-color: #2462d6;
              background-color: #2462d6;
            }
          }
        }
      }
    }

    &.search-detail-box-multi {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 20px;

      .search-bar {
        width: 100%;
        padding-left: 150px;
        justify-content: flex-start;

        .form-keyword {
          flex: 1;
          max-width: 780px;
        }
      }
    }
  }

  &-inner {
    display: flex;
    border-top: 1px dashed #dfdfdf;
    margin-top: 35px;
    padding-top: 20px;
  }

  &-title {
    flex: 0 0 140px;
    font-size: 19px;
    font-weight: bold;
    letter-spacing: -0.95px;
  }

  &-form {
    flex: 1;
  }

  &-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px 30px;
    padding-right: 130px;

    &-lg {
      grid-template-columns: repeat(3, 1fr);
    }

    .span-2 {
      grid-column: 1 / span 2;
    }

    .span-3 {
      grid-column: 1 / span 3;
    }

    .form-group {
      min-width: 0;
    }
  }

  &-actions {
    display: flex;
    gap: 5px;
    align-items: flex-end;

    .btn {
      .ico-magnify {
        width: 17px;
        height: 17px;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .search-detail {
    &-box {
      padding: 20px;
      border-radius: 10px;

      .search-option {
        flex-direction: column;

        .label {
          width: auto;
          margin-bottom: 10px;
        }

        .option-list {
          width: 100%;
        }
      }

      &.search-detail-box-multi {
        .search-bar {
          padding-left: 0;
          justify-content: center;

          .form-keyword {
            max-width: none;
          }
        }
      }
    }

    &-inner {
      flex-direction: column;
    }

    &-title {
      flex: none;
      margin-bottom: 20px;
    }

    &-grid {
      grid-template-columns: repeat(2, 1fr);
      gap: 15px;
      padding-right: 0;

      .span-3 {
        grid-column: auto;
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .search-detail {
    &-box {
      .search-bar {
        flex-direction: column;
      }
    }

    &-grid {
      grid-template-columns: repeat(1, 1fr);

      .span-2 {
        grid-column: auto;
      }
    }

    &-actions {
      justify-content: center;
    }
  }
}

.ui-info-list {
  display: flex;
  flex-direction: column;
  gap: 16px;

  > li {
    position: relative;
    padding-left: 32px;
    font-size: 17px;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
    }

    > .ui-info-list {
      gap: 8px;
      margin-top: 16px;

      > li {
        padding-left: 24px;
        font-size: 17px;

        > .ui-info-list {
          margin-top: 12px;

          > li {
            padding-left: 20px;
            font-size: 15px;
          }
        }
      }
    }
  }

  &.decimal {
    > li {
      &::before {
        content: '';
        width: 6px;
        height: 6px;
        background-color: $gray-222;
        border-radius: 100%;
        top: 10px;
        left: 8px;
      }
    }
  }

  &.dash {
    > li {
      &::before {
        content: '-';
      }
    }
  }

  &.hollow {
    > li {
      &::before {
        content: '';
        width: 4px;
        height: 4px;
        border: 1px solid $gray-222;
        border-radius: 100%;
        top: 9.5px;
        left: 8px;
      }
    }
  }

  &.ordered {
    > li {
      .num {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}