/*
  Tabs
*/

@use "variables" as *;
$tab-line-color: #666666;
$tab-line-point-color: #003675;

.tab-container {
  .tabs-box {
    margin-bottom: 30px;

    .tabs {
      display: flex;
      gap: 6px;

      .item {
        position: relative;
        flex: 1;
        background-color: $white;
        transition: .3s;
        word-break: keep-all;
        height: 53px;

        &::before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 2px;
          background-color: $tab-line-color;
          border-radius: 1px;
        }

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 4px;
          border-radius: 2px;
          background-color: $tab-line-point-color;
          transition: .3s;
          opacity: 0;
        }

        .tab-button {
          display: flex;
          align-items: flex-end;
          justify-content: center;
          width: 100%;
          height: 100%;
          text-align: center;
          transition: .3s;
          font-size: 19px;
          letter-spacing: -1.3px;
          color: $gray-666;
          font-weight: 500;
          padding: 0 20px 15px;
          transform-origin: center;
        }

        &.on {
          .tab-button {
            font-weight: bold;
            letter-spacing: -1.5px;
            color: $tab-line-point-color;
            font-size: 24px;
          }

          &::before {
            content: none;
          }

          &::after {
            opacity: 1;
          }
        }
      }
    }
  }

  .tab-content {
    .tab-panel {
      display: none;

      &.on {
        display: block;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .tab-container {
      .tabs-box {
        width: 100%;
        margin-bottom: 18px;

        .tabs {
          flex: 1;
        }
      }
    }
}

@media (max-width: $breakpoint-small) {
  .tab-container {
    .tabs-box {
      .tabs {
        .item {
          .tab-button {
            padding: 0 10px 15px;
            font-size: 16px;
          }

          &.on {
            .tab-button {
              font-size: 20px;
            }
          }
        }
      }
    }
  }
}

.section-filter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 30px;

  .item {
    position: relative;

    > * {
      font-size: 24px;
      letter-spacing: -1.2px;
      color: $gray-444;
    }

    &.on {
      > * {
        font-weight: bold;
        color: $gray-222;
      }

      &::before {
        content: '';
        position: absolute;
        top: -6px;
        left: 50%;
        transform: translateX(-50%);
        width: 6px;
        height: 6px;
        border-radius: 100%;
        background-color: #e4022e;
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .section-filter {
    gap: 10px 20px;
    width: 100%;
    justify-content: center;

    .item {
      font-size: 18px;
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .section-filter {
    gap: 10px 15px;

    .item {
      > * {
        font-size: 17px;
      }
    }
  }
}