/*
  Guide
*/

@use "variables" as *;

.guide-container {
  padding: 20px;

  .guide-heading {
    font-size: 50px;
    font-weight: bold;
    line-height: 60px;
    color: $point;
    margin-bottom: 20px;
    text-transform: uppercase;

    &::before {
      content: '';
    }
  }

  .guide-content {
    border: 1px solid #dfdfdf;
    border-radius: 10px;
    padding: 20px;
    min-height: calc(100vh - 40px);
  }
}